
'use client';

import { useState } from "react";
import { PageHeader } from "@/components/page-header";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Separator } from "@/components/ui/separator";
import { Truck, Printer, Download, Mail, Search, AlertCircle } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { useTripsStore } from "@/store/trips-store";
import type { Trip, VehicleType } from "@/lib/types";
import { Alert, AlertTitle, AlertDescription } from "@/components/ui/alert";

// Rate definitions from dispatch form for consistency
const passengerRates: Partial<Record<VehicleType, number>> = {
    "PREMIUM_SEDAN": 4.00, "SUV": 5.00, "MINI_BUS": 7.00, "SEDAN": 3.00,
    "LUXURY_CAR": 10.00, "LIGHT_BUS": 9.00, "MPV": 8.00, "HEAVY_BUS": 3.00,
};
const shipperRates: Partial<Record<VehicleType, number>> = {
    "TRUCK_3T": 10, "TRUCK_5T": 15, "TRUCK_7T": 20, "TRUCK_10T": 25, "TRUCK_15T": 30,
    "TRAILER": 35, "FLATBED": 35, "CONTAINER_40FT": 40, "CONTAINER_60FT": 50,
    "CRANE_TRAILER": 45, "FORKLIFT": 15, "MOBILE_CRANE": 40, "BACKHOE_LOADER": 20,
    "BOBCAT": 15, "DUMP_TRUCK": 25, "WATER_TRUCK": 18,
};

const FUEL_SURCHARGE_PERCENTAGE = 0.05; // 5%
const VAT_RATE = 0.15; // 15%

interface InvoiceDetails {
  id: string;
  date: string;
  dueDate: string;
  from: { name: string; address: string; email: string; };
  to: { name: string; address: string; email: string; };
  items: { id: number; description: string; quantity: number; rate: number; amount: number; }[];
  subtotal: number;
  fuelSurcharge: number;
  vat: number;
  total: number;
}

export default function InvoicePage() {
  const { toast } = useToast();
  const { trips } = useTripsStore();
  const [jobId, setJobId] = useState("");
  const [invoice, setInvoice] = useState<InvoiceDetails | null>(null);

  const handleGenerateInvoice = () => {
    if (!jobId) {
      toast({ variant: "destructive", title: "Job ID Required", description: "Please enter a Job ID to generate an invoice." });
      return;
    }

    const trip = trips.find(t => t.jobId.toLowerCase() === jobId.toLowerCase());

    if (!trip) {
      toast({ variant: "destructive", title: "Job Not Found", description: `No job found with ID: ${jobId}` });
      setInvoice(null);
      return;
    }
    
    if (trip.status !== 'COMPLETED') {
        toast({ variant: "destructive", title: "Job Not Completed", description: `Job ${jobId} is still in '${trip.status}' status.` });
        setInvoice(null);
        return;
    }

    // --- Calculation Logic ---
    const rates = trip.type === 'PASSENGER' ? passengerRates : shipperRates;
    const ratePerKm = trip.vehicle?.type ? rates[trip.vehicle.type] || 0 : 0;
    const baseFare = trip.fare || (ratePerKm * (trip.distance || 0));

    const fuelSurcharge = baseFare * FUEL_SURCHARGE_PERCENTAGE;
    const subtotal = baseFare + fuelSurcharge;
    const vat = subtotal * VAT_RATE;
    const total = subtotal + vat;
    
    const generatedInvoice: InvoiceDetails = {
      id: `INV-${trip.jobId}`,
      date: new Date().toLocaleDateString('en-CA'),
      dueDate: new Date(new Date().setDate(new Date().getDate() + 30)).toLocaleDateString('en-CA'),
      from: {
        name: "MHB Logistics",
        address: "123 Logistics Way, Riyadh, 11564, KSA",
        email: "billing@mhb-logistics.com",
      },
      to: { // Dummy data, replace with actual customer info later
        name: "Global Petro Services",
        address: "789 Industrial Ave, Dammam, 31442, KSA",
        email: "accounts@globalpetro.com",
      },
      items: [
        { id: 1, description: `Base Fare: ${trip.type} transport for Job ${trip.jobId}`, quantity: trip.distance || 1, rate: ratePerKm || baseFare, amount: baseFare },
        { id: 2, description: `Fuel Surcharge (${(FUEL_SURCHARGE_PERCENTAGE * 100).toFixed(0)}%)`, quantity: 1, rate: fuelSurcharge, amount: fuelSurcharge },
      ],
      subtotal: subtotal,
      fuelSurcharge: fuelSurcharge,
      vat: vat,
      total: total,
    };

    setInvoice(generatedInvoice);
    toast({ title: "Invoice Generated", description: `Invoice for Job ID ${trip.jobId} is ready.` });
  };

  return (
    <>
      <PageHeader
        title="Invoice Generator"
        description="Enter a completed Job ID to generate a detailed invoice."
        action={
            <div className="flex items-center gap-2">
                <div className="relative w-full max-w-sm">
                    <Label htmlFor="job-id" className="sr-only">Job ID</Label>
                    <Input id="job-id" placeholder="Enter Job ID..." value={jobId} onChange={(e) => setJobId(e.target.value)} />
                </div>
                <Button onClick={handleGenerateInvoice}>
                    <Search className="mr-2 h-4 w-4" /> Generate
                </Button>
            </div>
        }
      />
      
      {invoice ? (
        <Card className="max-w-4xl mx-auto animate-in fade-in-50 duration-500">
            <CardHeader>
            <div className="flex justify-between items-start">
                <div>
                <div className="flex items-center gap-2 font-bold text-2xl">
                    <Truck className="h-8 w-8 text-primary" />
                    <span>MHB Logistics</span>
                </div>
                </div>
                <div className="text-right">
                <h1 className="text-3xl font-bold text-primary">INVOICE</h1>
                <p className="text-muted-foreground">{invoice.id}</p>
                <div className="mt-4 flex items-center justify-end gap-2">
                    <Button variant="outline" size="sm"><Mail className="mr-2 h-4 w-4" /> Send</Button>
                    <Button variant="outline" size="sm"><Download className="mr-2 h-4 w-4" /> Download</Button>
                    <Button size="sm"><Printer className="mr-2 h-4 w-4" /> Print</Button>
                </div>
                </div>
            </div>
            </CardHeader>
            <CardContent className="space-y-8">
                <div className="grid sm:grid-cols-2 gap-4">
                    <div>
                        <h3 className="font-semibold mb-2">Billed To:</h3>
                        <address className="not-italic text-muted-foreground text-sm space-y-1">
                            <p className="font-bold text-foreground">{invoice.to.name}</p>
                            <p>{invoice.to.address}</p>
                            <p>{invoice.to.email}</p>
                        </address>
                    </div>
                    <div className="sm:text-right">
                        <h3 className="font-semibold mb-2">From:</h3>
                        <address className="not-italic text-muted-foreground text-sm space-y-1">
                            <p className="font-bold text-foreground">{invoice.from.name}</p>
                            <p>{invoice.from.address}</p>
                            <p>{invoice.from.email}</p>
                        </address>
                    </div>
                </div>

                <div className="grid sm:grid-cols-2 gap-4">
                    <div>
                        <h3 className="font-semibold mb-1">Invoice Date:</h3>
                        <p className="text-sm text-muted-foreground">{invoice.date}</p>
                    </div>
                    <div className="sm:text-right">
                        <h3 className="font-semibold mb-1">Due Date:</h3>
                        <p className="text-sm text-muted-foreground">{invoice.dueDate}</p>
                    </div>
                </div>

                <div>
                    <Table>
                        <TableHeader>
                            <TableRow>
                            <TableHead className="w-[60%]">Description</TableHead>
                            <TableHead className="text-center">Qty</TableHead>
                            <TableHead className="text-right">Rate</TableHead>
                            <TableHead className="text-right">Amount</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {invoice.items.map((item) => (
                            <TableRow key={item.id}>
                                <TableCell className="font-medium">{item.description}</TableCell>
                                <TableCell className="text-center">{item.quantity.toFixed(2)}</TableCell>
                                <TableCell className="text-right">{item.rate.toFixed(2)} SAR</TableCell>
                                <TableCell className="text-right">{item.amount.toFixed(2)} SAR</TableCell>
                            </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </div>
                
                <div className="flex justify-end">
                    <div className="w-full max-w-xs space-y-2">
                        <div className="flex justify-between">
                            <span className="text-muted-foreground">Subtotal</span>
                            <span>{invoice.subtotal.toFixed(2)} SAR</span>
                        </div>
                        <div className="flex justify-between">
                            <span className="text-muted-foreground">VAT ({ (VAT_RATE * 100).toFixed(0) }%)</span>
                            <span>{invoice.vat.toFixed(2)} SAR</span>
                        </div>
                        <Separator />
                        <div className="flex justify-between font-bold text-lg">
                            <span>Total</span>
                            <span>{invoice.total.toFixed(2)} SAR</span>
                        </div>
                    </div>
                </div>
            </CardContent>
            <CardFooter className="flex-col items-start gap-2 text-xs text-muted-foreground">
                <h3 className="font-semibold text-sm text-foreground">Notes</h3>
                <p>Please make all payments to the account details provided separately.</p>
                <p>Thank you for your business!</p>
            </CardFooter>
        </Card>
      ) : (
        <div className="flex items-center justify-center h-[50vh]">
            <Alert className="max-w-md text-center">
                <AlertCircle className="h-4 w-4"/>
                <AlertTitle>Awaiting Job ID</AlertTitle>
                <AlertDescription>
                    Enter a completed Job ID and click "Generate" to create an invoice.
                </AlertDescription>
            </Alert>
        </div>
      )}
    </>
  );
}
